import React, { useState, useEffect } from "react";
import { Button, Spin, Space } from "antd";
import { Share } from "components";
import AdSense from "react-adsense";

import Dragon from "assets/images/dragon.png";
import Fox from "assets/images/fox.png";
import Pegasaus from "assets/images/pegasaus.jpg";
import Phoenix from "assets/images/phoenix.jpg";
import Sphinx from "assets/images/sphinx.png";
import Unicorn from "assets/images/unicorn.png";

export const Result = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [text, setText] = useState("");
  const [name, setName] = useState("");
  const [total, setTotal] = useState(0);

  useEffect(() => {
    let score = 0,
      sum = 0;
    for (let i = 1; i <= 8; i += 1) {
      score = localStorage.getItem(`animal-${i}`)
        ? Number(localStorage.getItem(`animal-${i}`))
        : 1;
      sum += score;
    }
    setTotal(sum);

    if (100 <= sum && sum <= 160) {
      setName("");
      setText(
        "       .          .         .           ."
      );
    } else if (170 <= sum && sum <= 230) {
      setName("");
      setText(
        "      .          .          .        .     ."
      );
    } else if (240 <= sum && sum <= 300) {
      setName("");
      setText(
        "             .           .                 ."
      );
    } else if (310 <= sum && sum <= 370) {
      setName("");
      setText(
        "       .       .     .     .       .       ."
      );
    } else if (380 <= sum && sum <= 440) {
      setName("");
      setText(
        "           .         .            .  ,       ."
      );
    } else if (450 <= sum && sum <= 500) {
      setName("");
      setText(
        "            .       .        .         ."
      );
    }

    setTimeout(() => {
      setIsLoading(false);
    }, 2000);
  }, []);
  return (
    <div className="">
      {isLoading ? (
        <div className="pt-8 flex justify-center">
          <Space size="middle">
            <Spin size="large" />
          </Space>
        </div>
      ) : (
        <React.Fragment>
          <div className="pt-8">
            <div className="text-center text-2xl font-bold">
               ?
            </div>

            <AdSense.Google
              style={{ display: "block" }}
              client="ca-pub-9130836798889522"
              slot="3583038706"
              format="auto"
              responsive="true"
            />

            <div className="text-center pt-4 text-4xl">{name}</div>
            <div className="text-left px-4 pt-4 pb-4">{text}</div>
            {total >= 100 && total <= 160 ? (
              <img src={Dragon} className="w-full" alt="10" />
            ) : (
              ""
            )}
            {total >= 170 && total <= 230 ? (
              <img src={Unicorn} className="w-full" alt="20" />
            ) : (
              ""
            )}
            {total >= 240 && total <= 300 ? (
              <img src={Sphinx} className="w-full" alt="30" />
            ) : (
              ""
            )}
            {total >= 310 && total <= 370 ? (
              <img src={Pegasaus} className="w-full" alt="40" />
            ) : (
              ""
            )}
            {total >= 380 && total <= 440 ? (
              <img src={Fox} className="w-full" alt="50" />
            ) : (
              ""
            )}
            {total >= 450 && total <= 500 ? (
              <img src={Phoenix} className="w-full" alt="50" />
            ) : (
              ""
            )}
          </div>
          <Share />
          <div className="text-center">
            <div className="text-center pb-4 text-3xl">
                
            </div>
            <Button style={{ width: 336 }} type="primary" size="large">
              <a href="https://mbtilovetest.com">MBTI  </a>
            </Button>
            <Button
              className="mt-2"
              style={{ width: 336 }}
              type="primary"
              size="large"
            >
              <a href="https://color-age.inforever.xyz"> </a>
            </Button>
          </div>
        </React.Fragment>
      )}
    </div>
  );
};
