import React from "react";
import { Button, Radio, Space } from "antd";
import { Link } from "react-router-dom";
import { Adsense } from "components";

export const Page9 = () => {
  const title = "9.     ?";
  const data = [
    {
      score: 40,
      text: "  ",
    },
    {
      score: 10,
      text: "     ",
    },
    {
      score: 20,
      text: "   ",
    },
    {
      score: 30,
      text: " ",
    },
    {
      score: 50,
      text: ", , , ",
    },
  ];
  const doChange = (e) => {
    const { value } = e.target;
    localStorage.setItem("animal-9", value);
  };
  return (
    <div>
      <h1 className="pl-3 pr-2 pt-2 text-2xl font-bold">{title}</h1>

      <div className="pl-3 pt-3 pb-5">
        <Radio.Group className="font-default" onChange={doChange}>
          <Space direction="vertical">
            {data.map((item) => (
              <Radio value={item.score}>{item.text}</Radio>
            ))}
          </Space>
        </Radio.Group>
      </div>
      <Adsense />
      <div className="text-center mt-2">
        <Button style={{ width: 336 }} type="primary" size="large">
          <Link to="/page10"></Link>
        </Button>
      </div>
    </div>
  );
};
