import React, { useEffect } from "react";
import { message } from "antd";
import KakaoImage from "assets/images/kakao.png";
import FacebookImage from "assets/images/facebook.png";
import TwitterImage from "assets/images/twitter.png";
import LinkImage from "assets/images/link.png";

import ReactGA from "react-ga";

const success = () => {
  message.success("URL .");
};
export const Share = () => {
  const shareFacebook = () => {
    ReactGA.event({
      category: "SHARE",
      action: "click",
      label: "FACEBOOK",
    });
    window.open(
      `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(
        `https://animal.inforever.xyz`
      )}&t=  !!`,
      "_blank",
      "width=600, height: 400"
    );
  };
  const copy = () => {
    ReactGA.event({
      category: "SHARE",
      action: "click",
      label: "LINK",
    });
    success();
    var textarea = document.createElement("textarea");
    textarea.value = `https://animal.inforever.xyz`;

    document.body.appendChild(textarea);
    textarea.select();
    textarea.setSelectionRange(0, 9999); // 

    document.execCommand("copy");
    document.body.removeChild(textarea);
  };
  const createKakaoButton = () => {
    ReactGA.event({
      category: "SHARE",
      action: "click",
      label: "KAKAO",
    });
    // kakao sdk script   window.Kakao  
    if (window.Kakao) {
      const kakao = window.Kakao;
      //  initialization 
      if (!kakao.isInitialized()) {
        //  step   javascript key   initialize
        kakao.init("d9aedb635913dd74c823a5761b497b37");
      }
      kakao.Link.createDefaultButton({
        // Render  id=kakao-link-btn     
        container: "#kakao-link-btn",
        objectType: "feed",
        content: {
          title: " ",
          description: "    .",
          imageUrl: "https://animal.inforever.xyz/logo.png",
          link: {
            mobileWebUrl: "https://animal.inforever.xyz",
            webUrl: "https://animal.inforever.xyz",
          },
        },
        buttons: [
          {
            title: " ",
            link: {
              mobileWebUrl: "https://animal.inforever.xyz",
              webUrl: "https://animal.inforever.xyz",
            },
          },
        ],
      });
    }
  };
  useEffect(() => {
    createKakaoButton();
  }, []);
  return (
    <>
      <div className="flex justify-center px-4 pt-4 pb-4">
        <div
          className="twitter-share-button inline"
          style={{ cursor: "pointer" }}
          onClick={() => {
            ReactGA.event({
              category: "SHARE",
              action: "click",
              label: "TWITTER",
            });
          }}
        >
          <a
            href={`https://twitter.com/intent/tweet?text= ?&url=https://animal.inforever.xyz&hashtags=##}`}
            target="_blank"
            rel="noreferrer"
          >
            <img
              src={TwitterImage}
              alt="twitter-share-icon"
              style={{ width: 90 }}
            />
          </a>
        </div>
        <div
          className="facebook-share-button pt-1 inline"
          onClick={() => shareFacebook()}
          style={{ cursor: "pointer" }}
        >
          <img
            src={FacebookImage}
            alt="facebook-share-icon"
            style={{ width: 80 }}
          />
        </div>
        <div
          className="kakao-share-button inline"
          style={{ cursor: "pointer" }}
        >
          <button id="kakao-link-btn">
            <img
              src={KakaoImage}
              alt="kakao-share-icon"
              style={{ width: 90 }}
            />
          </button>
        </div>

        <div
          className="link-share-button inline pt-2"
          onClick={() => copy()}
          style={{ cursor: "pointer" }}
        >
          <img src={LinkImage} alt="link-share-icon" style={{ width: 70 }} />
        </div>
      </div>
    </>
  );
};
